import { reactShinyInput } from 'reactR';
import { useEffect } from "react";
import { defaultChains, InjectedConnector, Provider, useAccount, useConnect } from "wagmi";
import { WalletConnectConnector } from "wagmi/connectors/walletConnect";

export function EvmWalletConnectors({ setValue }) {
  // Chains for connectors to support
  const chains = defaultChains;

  // Set up connectors
  const connectors = () => {
    return [
      new InjectedConnector({ chains }),
      new WalletConnectConnector({
        options: {
          qrcode: true,
        },
      }),
    ];
  };

  return (
    <Provider autoConnect connectors={connectors}>
      <Connectors setWalletAddress={setValue} />
    </Provider>
  );
}

function Connectors({ setWalletAddress }) {
  const [{ data: accountData }, disconnect] = useAccount({
    fetchEns: true,
  });
  useEffect(() => {
    setWalletAddress(accountData?.address ?? "");
  }, [accountData?.address]);

  const [{ data }, connect] = useConnect();

  if (accountData) {
    return (
      <button type="button" onClick={() => disconnect()}>
        Disconnect
      </button>
    );
  }

  return (
    <>
      <div>
        {data.connectors.map((c) => {
          if (!c.ready) {
            // Metamask not installed, for example
            return;
          }
          return (
            <button type="button" disabled={!c.ready} key={c.id} onClick={() => connect(c)}>
              {c.name}
            </button>
          );
        })}
      </div>
    </>
  );
}

reactShinyInput('.metamaskConnect', 'metamaskConnectr.metamaskConnect', EvmWalletConnectors);
